import ui from "./UI";
import { stages } from "./util";
import { cpus } from "./db/CPU";
import { softwares } from "./db/softwares";
import lowGpus from "./db/GPU.Low";
import middleGpus from "./db/GPU.Middle";
import highGpus from "./db/GPU.High";
import { rams } from "./db/rams";
import { hards } from "./db/hard";
import Score from "./Score";

class Main {
  constructor() {
    let gpus = JSON.parse(lowGpus).concat(JSON.parse(middleGpus));
    gpus = gpus.concat(JSON.parse(highGpus));
    this.state = {
      CPU: JSON.parse(cpus),
      "نرم افزار": JSON.parse(softwares),
      "کارت گرافیک": gpus,
      RAM: JSON.parse(rams),
      هارد: JSON.parse(hards),
    };

    this.selected = {};
    this.currentStage = "نرم افزار";
    this.stages = ["نرم افزار", "CPU", "کارت گرافیک", "RAM", "هارد", "نتیجه"];
    this.finalScore = 0;
  }

  getOptions(type) {
    if (this.currentStage !== "نتیجه") {
      ui.drawOptions(this.state[type], type);
    }
  }

  getResults() {
    const selected = { ...this.selected };
    const software = this.getSoftware(selected["نرم افزار"]);
    const hardwares = {
      CPU: this.findItem(this.state.CPU, selected.CPU),
      "کارت گرافیک": this.findItem(
        this.state["کارت گرافیک"],
        selected["کارت گرافیک"]
      ),
      RAM: this.findRam(this.state.RAM, selected.RAM),
      هارد: this.findItem(this.state["هارد"], selected["هارد"]),
    };

    const scores = [];

    for (const hardware in hardwares) {
      if (hardware === "CPU" || hardware === "کارت گرافیک") {
        scores.push(Score.calcCpuAndGpuScore(hardwares[hardware]));
      } else {
        scores.push(hardwares[hardware].score);
      }
    }

    this.calcAvgScore(scores);
    ui.drawResults(software, hardwares);
    ui.drawScore(this.finalScore);
  }

  getSoftware(name) {
    return this.state["نرم افزار"].find((software) => software.name === name);
  }

  calcAvgScore(scores) {
    let sum = 0;

    scores.forEach((score) => {
      sum += score;
    });

    this.finalScore = sum / scores.length;
  }

  nextStage(e) {
    if (ui.hardwareList.selectedIndex === 0) {
      ui.feedback.style.display = 'block';
    } else {
      ui.feedback.style.display = 'none';
      this.saveUserOption();
      const stage = e.target.dataset.stage;
      this.currentStage = stage;
      const stageIndex = this.stages.indexOf(stage);
      ui.title.textContent = `انتخاب ${stage}`;
      ui.stage.textContent = `قدم ${stages[stageIndex]}`;
      ui.clearList();
      ui.enableButton(ui.prevStep, false);

      if (stageIndex === this.stages.length - 1) {
        ui.enableButton(ui.nextStep, true);
        ui.nextStep.dataset.stage = "";
      } else {
        ui.nextStep.dataset.stage = this.stages[stageIndex + 1];
      }

      ui.prevStep.dataset.stage = this.stages[stageIndex - 1];
      ui.setProgressWidth(
        ui.getProgressWidth() + 100 / (this.stages.length - 1)
      );
      const stageElements = document.querySelectorAll(".badge");
      stageElements[stageIndex].classList.replace("bg-white", "badge-primary");

      if (stage === "نتیجه") {
        this.getResults();
      } else {
        this.getOptions(stage);
      }
      ui.clearFilter();
    }
  }

  prevStage(e) {
    const stage = e.target.dataset.stage;
    this.currentStage = stage;
    const stageIndex = this.stages.indexOf(stage);
    ui.title.textContent = `انتخاب ${stage}`;
    ui.stage.textContent = `قدم ${stages[stageIndex]}`;
    ui.clearList();
    ui.enableButton(ui.nextStep, false);

    if (stageIndex === 0) {
      ui.enableButton(ui.prevStep, true);
      ui.prevStep.dataset.stage = "";
    } else {
      ui.prevStep.dataset.stage = this.stages[stageIndex - 1];
    }

    ui.nextStep.dataset.stage = this.stages[stageIndex + 1];
    ui.setProgressWidth(ui.getProgressWidth() - 100 / (this.stages.length - 1));
    const stageElements = document.querySelectorAll(".badge");
    stageElements[stageIndex + 1].classList.replace(
      "badge-primary",
      "bg-white"
    );
    this.getOptions(stage);
    ui.clearFilter();
  }

  filterOptions(e) {
    const keyword = e.target.value.toLowerCase();

    if (keyword) {
      let list = this.state[this.currentStage];
      list = list.filter((item) => item.name.toLowerCase().includes(keyword));
      ui.drawOptions(list);
    } else {
      ui.drawOptions(this.state[this.currentStage]);
    }
  }

  saveUserOption() {
    const optionIndex = ui.hardwareList.selectedIndex;
    const selectedValue = ui.hardwareList.options[optionIndex].value;
    this.selected[this.currentStage] = selectedValue;
  }

  findItem(list, keyword) {
    return list.find((item) => item.name === keyword);
  }

  findRam(list, value) {
    let [name, storage] = value.split(" ");
    storage = parseInt(storage);

    return list.find((item) => item.name === name && item.storage === storage);
  }

  getScore(category) {
    switch (category) {
      case "high":
        return 3;
      case "middle":
        return 2;
      case "low":
        return 1;
    }
  }

  init() {
    ui.setProgressWidth(0);
    document.addEventListener("DOMContentLoaded", this.getOptions("نرم افزار"));
    ui.nextStep.addEventListener("click", this.nextStage.bind(this));

    ui.prevStep.addEventListener("click", this.prevStage.bind(this));
    ui.filter.addEventListener("keyup", this.filterOptions.bind(this));
  }
}

const main = new Main();
main.init();
